</template>

<script>
	export default {
		data () {
			return {
			}
		}
	}
</script>

<style scoped>
	.flex{
	  display: flex;
	}
	.direction-column{
		flex-direction: column;
	}
	.light{
		color: #fa8a12;
	}
</style>



